import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { ICommand, RxCommandFactoryService } from '@helix/platform/shared/api';

@Injectable({
  providedIn: 'root'
})
export class RxEventStatisticsDefinitionService {
  constructor(private rxCommandFactoryService: RxCommandFactoryService) {}

  revertCustomization(eventStatisticsDefinitionName: string): Observable<any> {
    const revertCustomizationCommand: ICommand = this.rxCommandFactoryService.forResourceType(
      'com.bmc.arsys.rx.application.event.command.RevertEventStatisticsDefinitionCommand'
    );

    return revertCustomizationCommand.execute({ eventStatisticsDefinitionName });
  }
}
